.projects-container {
  padding-top: 100px;
  padding-bottom: 60px;
}

.projects-title {
  text-align: center;
  margin-bottom: 40px;
}

.projects-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 30px;
  padding: 20px 0;
}

@media (max-width: 600px) {
  .projects-container {
    padding-top: 80px;
  }

  .projects-grid {
    grid-template-columns: 1fr;
    gap: 20px;
  }
}
